.container {
  height: 100%;
  display: flex;
  justify-content: center;
  margin-top: 50px;
}

.movie__detail {
  padding: 50px;
  width: 80%;
  padding-top: 70px;
  background-color: white;
  margin-bottom: 70px;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  color: #adaeb9;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie__detail img {
  position: relative;
  top: -50px;
  max-width: 150px;
  width: 100%;
  margin-right: 30px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie__detail .movie__title,
.movie__detail .movie__year {
  margin: 0;
  font-weight: 300;
}

.movie__detail .movie__title {
  margin-bottom: 5px;
  font-size: 24px;
  color: #2c2c2c;
}
